<template>


  <el-carousel ref="carousel"  :interval="2000" type="card" height="300px" trigger="click" :autoplay="true" @click.native="clickPicture">
    <el-carousel-item v-for="item in 6" :key="item">
      <h3 class="medium">{{ item }}</h3>
    </el-carousel-item>
  </el-carousel>

</template>

<style>
 .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>

<script>
export default {
  name: "Main",
  data() {
      return {
        activeIndex: '/main',
        imgs:[]
      };
  },
  methods: {
    clickPicture(){
      console.log('图片：'+this.$refs.carousel.activeIndex+"被点击")
    }

  },
 computed: {
    isLogin(){
      if(sessionStorage.getItem("userid") && sessionStorage.getItem("userToken")){
        this.$$store.commit("userStatus",sessionStorage.getItem("userid"))
      }else{
        this.$$store.commit("userStatus",null)
      }
      return this.$store.getters.isLogin
    }
  },
  mounted() {
    // this.getPicture()
    console.log("下面是vuex的信息")
    console.log(this.$store.state.currentUser)
    console.log(sessionStorage.getItem('userToken')+"+"+sessionStorage.getItem("userid"))

  },
};
</script>
